<template>
  <div class="detail-container">
    <div class="search">
      <div class="concatField">
        <el-input
          placeholder="请输入关键字"
          v-model="id"
          clearable>
        </el-input>
        <el-button type="primary" @click="_search">搜索</el-button>
      </div>
    </div>
    <div class="table">
      <el-table
        border
        :data="tableData"
        style="width: 100%">
        <el-table-column
          prop="user.id"
          label="id"
          width="180">
        </el-table-column>
        <el-table-column
          prop="user.username"
          label="用户名"
          width="180">
        </el-table-column>
        <el-table-column
          prop="user.password"
          label="密码">
        </el-table-column>
        <el-table-column
          label="权限">
          <template slot-scope="scope">
            <!--<span v-for="item in scope.row.permissions">{{item}}</span>-->
            <el-checkbox-group v-model="scope.row.permissions">
              <el-checkbox label="insert" >新增</el-checkbox>
              <el-checkbox label="delete"> 删除</el-checkbox>
              <el-checkbox label="update">更新</el-checkbox>
            </el-checkbox-group>
          </template>
        </el-table-column>
        <el-table-column
          label="操作"
          fixed="right"
        width="100">
          <template slot-scope="scope">
            <el-button @click="modifyUser(scope.row)" type="warning" size="small">修改</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>

<script>
  import {search} from 'api/user'
  import * as config from 'api/config'
  export default {
    name: 'userList',
    data () {
      return {
        tableData: [],
        id: ''
      }
    },
    mounted () {
      this._search()
    },
    methods: {
      _search () {
        search(this.id).then(res => {
          if (res.data.status === config.ERR_OK) {
            this.tableData = res.data.data
          }
        })
      },
      modifyUser (row) {
        console.log(row)
      }
    }
  }
</script>

<style scoped lang="stylus">
  .detail-container
    .search
      .concatField
        overflow hidden
        .el-input
          width 400px
    .table
      margin 30px 0
</style>



